import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
/* eslint-disable no-unused-vars */
import VueRouter from 'vue-router'
Vue.use(VueRouter)
Vue.config.productionTip = false

import Dashboard from "./components/Dashboard.vue";
import Settings from "./components/Settings.vue";
import Sensors from "./components/Sensors.vue";
import Cameras from "./components/Cameras.vue";

function httpGetAsync(theUrl, callback) {
  var xmlHttp = new XMLHttpRequest();
  xmlHttp.onreadystatechange = function () {
      if (xmlHttp.readyState == 4 && xmlHttp.status == 200)
          callback(xmlHttp.responseText);
  }
  xmlHttp.open("GET", theUrl, true); // true for asynchronous 
  xmlHttp.send(null);
}

//httpGetAsync(window.location.href + "gateway", (callback) => {
//  console.log(callback);
//  window.websocket = callback;
//})
window.webready = false;
httpGetAsync("http://192.168.1.182:4750/" + "gateway", (callback) => {
  console.log(callback);
  window.websocket = new WebSocket(callback);
  window.websocket.addEventListener('message', function (event) {
    var json = JSON.parse(event.data);
    window.json = json;
  });
  window.webready = true;
})
var delay = 1000;

setTimeout(function() {
  window.streamready = false;
  httpGetAsync("http://192.168.1.182:4750/" + "stream", (callback) => {
    console.log(callback);
    window.stream = new WebSocket(callback);
    window.stream.addEventListener('message', function (event) {
      var json = JSON.parse(event.data);
      window.strjson = json;
    });
    window.streamready = true;
  })
}, delay);

//window.websocket = "ws://192.168.1.245:9000/";
const routes = [
    { path: '/dashboard', component: Dashboard },
    { path: '/settings', component: Settings },
    { path: '/sensors', component: Sensors},
    { path: '/cameras', component: Cameras},
]

const router = new VueRouter({
    routes: routes
})

var app = new Vue({
  vuetify,
  render: h => h(App),
  router
}).$mount('#app')
/* eslint-enable no-unused-vars */