<template>
    <v-app id="inspire">
        <v-row>
            <v-col class="justify-center" style="max-width: 215px">
                <v-card class="ma-2">
                    <v-card-title>
                        Odometry
                    </v-card-title>
                    <v-card-text>
                        Left: {{ odometry.left }} <br />
                        Right: {{ odometry.right }} <br />
                        Aux: {{ odometry.aux }}
                    </v-card-text>
                </v-card>
                <v-card class="ma-2">
                    <v-card-title>
                        Gyro
                    </v-card-title>
                    <v-card-text>
                        Heading: {{ gyro.heading }} <br />
                        Pitch: {{ gyro.pitch }} <br />
                        Roll: {{ gyro.roll }}
                    </v-card-text>
                </v-card>
                <v-card class="ma-2">
                    <v-card-title>
                        Position
                    </v-card-title>
                    <v-card-text>
                        X: {{ position.x }} <br />
                        Y: {{ position.y }} <br />
                        Heading: {{ position.r }}
                    </v-card-text>
                </v-card>
            </v-col>
            <v-col class="justify-center" style="max-width: 215px">
                <v-card class="ma-2">
                    <v-card-title>
                        Intake Tripwire
                    </v-card-title>
                    <v-card-text>
                        Distance: {{ tripwire }}
                    </v-card-text>
                </v-card>
            </v-col>
            <v-col>
                <v-card id="fieldVcard" :loading="true" class="ml-6">
                    <v-responsive :aspect-ratio="2/2" v-resize="resizeCanvas">
                        <img src="field2.png" style="height:100%" ref="FieldImage">
                        <v-container style="padding: 0;">
                            <canvas id="fieldCanvas" ref="canvas" width="500" height="500"></canvas>
                        </v-container>
                    </v-responsive>
                </v-card>
            </v-col>
        </v-row>
    </v-app>
</template>
<script>
    var robotInterval;
    export default {
        props: {
            source: String,
        },
        data: () => ({
            odometry: { left: 0, right: 0, aux: 0 },
            gyro: { heading: 0, pitch: 0, roll: 0 },
            tripwire: 0,
            position: { x: 80, y: 80, r: 40 },
        }),
        created() {
        },
        mounted() {
            robotInterval = setInterval(() => {
                var ctx = this.$refs.canvas.getContext('2d');
                const robot = new Image();
                robot.src = "robot.png";
                robot.onload = () => {
                    var sizeWidth = ctx.canvas.width;
                    var sizeHeight = ctx.canvas.height;

                    var scaleWidth = sizeWidth / 141;
                    var scaleHeight = sizeHeight / 141;

                    ctx.save();
                    ctx.translate((parseFloat(this.position.x) * scaleWidth), (parseFloat(this.position.y) * scaleHeight));
                    ctx.rotate(parseFloat(this.position.r) * Math.PI / 180);
                    ctx.drawImage(robot, -(scaleWidth * 9), -(scaleHeight * 9), scaleWidth * 18, scaleHeight * 18);
                    ctx.restore();
                };
            }, 50);
        },
        beforeDestroy() {
            clearInterval(robotInterval);
        },
        methods: {
            resizeCanvas() {
                var ctx = this.$refs.canvas.getContext('2d');
                ctx.canvas.width = this.$refs.FieldImage.width;
                ctx.canvas.height = this.$refs.FieldImage.height;
                ctx.webkitImageSmoothingEnabled = false;
                ctx.mozImageSmoothingEnabled = false;
                ctx.imageSmoothingEnabled = false;
            }
        }
    }
</script>
<style>
    img {
        padding: 0;
        display: block;
        margin: 0 auto;
        max-height: 80vh;
        height: auto;
        max-width: 80vh;
        z-index: 1;
        position: absolute;
    }

    #fieldVcard {
        padding: 0;
        display: block;
        margin: 0 auto;
        max-height: 80vh;
        height: auto;
        max-width: 80vh;
        width: 100vh;
    }

    #fieldCanvas {
        padding: 0;
        z-index: 20;
        position: relative;
        image-rendering: optimizeSpeed;
        image-rendering: -moz-crisp-edges;
        image-rendering: -webkit-optimize-contrast;
        image-rendering: -o-crisp-edges;
        image-rendering: crisp-edges;
        -ms-interpolation-mode: nearest-neighbor;
    }
</style>