<template>
    <v-app id="inspire">
        <v-row>
            <v-col>
                <v-card id="camerasCard" class="ml-6">
                    <v-responsive :aspect-ratio="2/2" v-resize="resizeCanvas">
                        <v-container style="padding: 0;">
                            <img src="" style="height:50%" ref="img">
                        </v-container>
                    </v-responsive>
                </v-card>
            </v-col>
        </v-row>
    </v-app>
</template>
<script>
/* eslint-disable no-unused-vars */
    var camera;
    var socket;
    var testData;
    export default {
        props: {
            source: String,
        },
        data: () => ({
            odometry: { left: 0, right: 0, aux: 0 },
            gyro: { heading: 0, pitch: 0, roll: 0 },
            tripwire: 0,
            position: { x: 80, y: 80, r: 40 },
        }),
        created() {
        },
        mounted() {
            this.$refs.img.onerror = function(){
                console.log("Test Error");
            };
            var image = new Image();
            camera = setInterval(() => {
                var base = "data:image/png;base64,";
                if(window.streamready){
                    var json = window.strjson;
                    if(json.opcode === 6){
                        //testData = json.stream.replace(/(\r\n|\n|\r)/gm, "");
                        testData = base.concat(json.stream);
                        if(!testData.includes("null")){
                            image.src=testData;
                            image.onload = () => {
                                this.$refs.img.src = testData;
                            };
                        }
                    }
                }
            }, 17);
        },
        beforeDestroy() {
            clearInterval(camera);
            socket.close()
        },
        methods: {
            intToHex(number){
                return "#"+((number)>>>0).toString(16).slice(-6);
            },
        }
    }
/* eslint-enable no-unused-vars */
</script>