<template>
    <v-app id="inspire">
        <v-row>
            <v-divider vertical class="mx-4" :light="this.$vuetify.theme.dark" :dark="!this.$vuetify.theme.dark"></v-divider>
            <v-card fluid class="ma-5" min-width="20vw">
                <v-card-title class="justify-center">
                    Telemetry
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text v-html="telemetryText"></v-card-text>
            </v-card>
            <v-card fluid class="ma-5" min-width="20vw">
                <v-card-title class="justify-center">
                    Opmodes
                </v-card-title>
                <v-divider></v-divider>
                <v-card-actions class="text-center">
                    <v-col>
                        <v-list v-for="item in opmodes" :key="item.name">
                            <v-btn min-width="20vw" @click="selectOpmode(item.name)">
                                {{ item.name }}
                            </v-btn>
                        </v-list>
                    </v-col>
                </v-card-actions>
            </v-card>
            <v-card fluid class="ma-5" min-width="20vw">
                <v-card-title class="justify-center">
                    Robot Console
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text class="text-center" fluid>
                    <v-container>
                        Selected Opmode: {{ selectedOpmode }}
                    </v-container>
                    <v-row>
                        <v-btn color="blue" class="ma-3" min-width="20vw" :disabled="selectedOpmode === 'None'" @click="initOpmode()">
                            Init Opmode
                        </v-btn>
                    </v-row>
                    <v-row>
                        <v-btn color="green" class="ma-3" min-width="20vw" :disabled="selectedOpmode === 'None'" @click="startOpmode()">
                            Start Opmode
                        </v-btn>
                    </v-row>
                    <v-row>
                        <v-btn color="red" class="ma-3" min-width="20vw" :disabled="selectedOpmode === 'None'" @click="stopOpmode()">
                            Stop Opmode
                        </v-btn>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-row>
    </v-app>
</template>

<script>
var socket;
var getOpmodes;
var updateText;
    export default {
        props: {
            source: String,
        },
        data: () => ({
            telemetryText: "Foo<br/>Bar",
            opmodes: [{ name: "Test1" }, { name: "Test2" }],
            selectedOpmode: "None",
            ping: "0ms",
        }),
        created() {
            
        },
        mounted(){
            getOpmodes = setInterval(function(){
                window.websocket.send("OPMODES");
            }.bind(this), 1000);
            updateText = setInterval(() => {
                var json = window.json;
                if(!(json == null)){
                    if(json.opcode === 1){
                        this.telemetryText = json.telemetry;
                    }
                    if(json.opcode === 2){
                        var opcodeArr = json.opmodes;
                        var opcodeI;
                        while(this.opmodes.length > 0) {this.opmodes.pop()}
                        for (opcodeI in opcodeArr) {
                            this.opmodes.push({ name: opcodeArr[opcodeI] });
                        }
                    }
                }
            }, 150)
        },
        beforeDestroy() {
            socket.close()
            clearInterval(getOpmodes);
            clearInterval(updateText);
        },
        methods: {
            selectOpmode(name){
                this.selectedOpmode = name;
            },
            initOpmode() {
                window.websocket.send("INIT " + this.selectedOpmode);
            },
            startOpmode() {
                window.websocket.send("START");
            },
            stopOpmode() {
                window.websocket.send("STOP");
            },
        }
    }
</script>