<template>
    <v-app id="inspire">
        <v-navigation-drawer v-model="drawer"
                             app
                             clipped>
            <v-list dense v-for="item in routes" :key="item.name">
                <v-list-item link :to="item.link">
                    <v-list-item-action>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>{{ item.name }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar app
                   clipped-left dense>
            <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
            <v-toolbar-title>Dashboard v1.3</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn @click="switchTheme">
                {{ theme }}
            </v-btn>
        </v-app-bar>

        <v-main>
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </v-main>
    </v-app>
</template>

<script>
    export default {
        props: {
            source: String,
        },
        data: () => ({
            drawer: false,
            routes: [
                { name: "Dashboard", icon: "mdi-view-dashboard", link: "/dashboard" },
                { name: "Settings", icon: "mdi-cog", link: "/settings" },
                { name: "Sensors", icon: "mdi-radar", link: "/sensors"},
                { name: "Cameras", icon: "mdi-camera", link:"/cameras"}
            ],
            theme: "Light Theme",
        }),
        created() {
            this.$vuetify.theme.dark = true;
        },
        methods: {
            switchTheme() {
                this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
                if (this.$vuetify.theme.dark) {
                    this.theme = "Light Theme";
                } else {
                    this.theme = "Dark Theme";
                }
            }
        }
    }
</script>
